import ValidCode from '../../../components/validCode.vue'
import { getCode, phoneLogin, getTokenData,checkCode, updatePwd, passwordLogin} from '../../../api/login.js'

export default {
	components:{
		ValidCode
	},
	data(){
		return {
			step:1, // 1 输入验证码 2设置密码 
			isCodeValid:false,
			isPwdValid:false,
			phone:undefined,
			cfmCode:undefined,
			maxlength:6,
			seconds:59,
			password:undefined,
			pwdType:'password',
			onSend:false,
			title:'输入验证码',
		}
	},
	watch:{
		step(val){
			switch(val){
				case 1:
				this.title = '输入验证码'
				break
				case 2:
				this.title = '设置密码'
				break 
				default:
				break
				
			}
		}
	},
	onShow() {
		this.getCode()
	},
	onLoad() {
		let userInfo = uni.getStorageSync('userInfo')
		this.phone = userInfo.phone
	},
	methods:{
		regPwd(e){
			let val = e&&e.detail.value || e&&e.target.value || this.password
			if(val && val!=''){
				this.isPwdValid = true
			}else{
				this.isPwdValid = false
			}
		},
		clearPhone(){
			this.phone = undefined
			this.regPhone()
		},
		regCode(finish,v){
			// console.log(finish,v);
			this.isCodeValid = finish ? true : false
			this.cfmCode = v
		},
		// 获取验证码
		getCode(){
			let timer = setInterval(()=>{
				if(this.seconds==0){
					clearInterval(timer)
					this.onSend = false
					return
				}
				this.seconds--
			},1000)
			//设置密码 type 2
			getCode({
				phone:this.phone,
				type:2
			}).then(res=>{
				uni.showToast({
					title:res.message
				})
			})
		},
		// 设置密码 验证手机号码
		checkCode(){
			if(!this.isCodeValid) return
			checkCode({
				phone:this.phone,
				code:this.cfmCode
			}).then(res=>{
				if(res.code==1){
					this.step = 2
				}
			})
		},
		changPwdType(){
			this.pwdType = this.pwdType=='password' ? 'text' : 'password' 
		},
		// 设置密码
		updatePwd(){
			updatePwd({
				phone:this.phone,
				pwd:this.password
			}).then(res=>{
				if(res.code==1){
					console.log('密码修改成功');
					uni.showToast({
						title: res.message,
						icon:"none"
					});
					uni.removeStorageSync('isLogin')
					uni.removeStorageSync('token')
					uni.removeStorageSync('userInfo')
					uni.switchTab({
						url:"/pages/Tabbar/mine/index"
					})
				}
			})
		}
		
	}
}
